import axios from 'axios'
import * as types from './mutation-types.js'
import MockAdapter from 'axios-mock-adapter'



export default {
  userLogin({
    commit,
    state
  }, userinfo) {
    commit(types.IS_LOGIN, true)
    commit(types.GET_USERINFO, userinfo)
  },
  getSliders({
    commit,
    state
  }) {
    let mock = new MockAdapter(axios)
    mock.onGet('/sliders').reply(200, {
      "code": "0",
      "message": "",
      "list": [{
        "url": "",
        "img": "http://cdn2.ettoday.net/images/1624/1624023.jpg",
        "title": ""
      }, {
        "url": "",
        "img": "http://pic.pimg.tw/realestates/1424788034-438499438.jpg?v=1424788036",
        "title": ""
      }, {
        "url": "",
        "img": "https://i.ytimg.com/vi/5nZ8lIhG3Nw/maxresdefault.jpg",
        "title": ""
      }]
    })

    axios.get('/sliders').then(response => {
      if (response.data.list) {
        commit(types.GET_SLIDERS, response.data.list)
      }
    })
  },
  getHouses({
    commit,
    state
  }) {
    let mock = new MockAdapter(axios)
    mock.onGet('/houselist').reply(200, {
      "code": "0",
      "message": "",
      "list": [{
        "id": 1,
        "name": "",
        "address": "160106"
      }, {
        "id": 2,
        "name": "",
        "address": "3"
      }, {
        "id": 3,
        "name": "",
        "address": "201106"
      }, {
        "id": 4,
        "name": "",
        "address": "393104"
      }]
    })
    axios.get('/houselist').then(response => {
      if (response.data.list) {
        commit(types.GET_HOUSES, response.data.list)
      }
    })
  },
  getHouse_datail({
    commit,
    state
  }) {

  },
  getRooms({
    commit,
    state
  }) {
    let mock = new MockAdapter(axios)
    mock.onGet('/roomlist').reply(200, {
      "code": "0",
      "message": "",
      "list": [{
        "id": 1,
        "floor": 1,
        "room_no": 1,
        "tenant": "",
        "start_date": "2011/09/07",
        "expiry_date": "2017/11/12",
        "monthly_date": "12",
        "deposit": 14000,
        "monthly_rent": 7000,
        "first_rent": 7500,
        "isRent": true,
        "notes": ""

      }, {
        "id": 2,
        "floor": 1,
        "room_no": 2,
        "tenant": "",
        "start_date": "2015/04/02",
        "expiry_date": "2017/05/12",
        "monthly_date": "25",
        "deposit": 15000,
        "monthly_rent": 7500,
        "first_rent": 7500,
        "isRent": true,
        "notes": ""
      }, {
        "id": 3,
        "floor": 1,
        "room_no": 3,
        "tenant": "",
        "start_date": "2011/01/27",
        "expiry_date": "2017/05/12",
        "monthly_date": "05",
        "deposit": 15000,
        "monthly_rent": 7500,
        "first_rent": 7500,
        "isRent": true,
        "notes": ""
      }, {
        "id": 4,
        "floor": 2,
        "room_no": 1,
        "tenant": "",
        "start_date": "2013/11/12",
        "expiry_date": "2017/05/12",
        "monthly_date": "22",
        "deposit": 13000,
        "monthly_rent": 6500,
        "first_rent": 6500,
        "isRent": true,
        "notes": ""
      }, {
        "id": 5,
        "floor": 2,
        "room_no": 2,
        "tenant": "",
        "start_date": "2013/09/22",
        "expiry_date": "2017/05/12",
        "monthly_date": "26",
        "deposit": 12000,
        "monthly_rent": 6000,
        "first_rent": 6000,
        "isRent": true,
        "notes": ""
      }, {
        "id": 6,
        "floor": 3,
        "room_no": 1,
        "tenant": "",
        "start_date": "",
        "expiry_date": "",
        "monthly_date": "",
        "deposit": 0,
        "monthly_rent": 0,
        "first_rent": 0,
        "isRent": false,
        "notes": ""
      }]
    })
    axios.get('/roomlist').then(response => {
      if (response.data.list) {
        commit(types.GET_ROOMS, response.data.list)
      }
    })
  }
}



